.sidebar {
  max-width: 270px;
  width: 100%;
  min-height: 100%;
  background-color: $lgray;
  position: absolute;
  right: 0;
  top: 0;
  transition: .25s;
  &__inner {
    display: flex;
    flex-direction: column;
  }
  &__filter {}
  &__burger {
    margin-left: 15px;
    width: 30px;
    cursor: pointer;
    & > span {
      width: 30px;
      height: 2px;
      margin: 8px 0;
      background-color: #fff;
      display: block;
      transition: .25s;
    }
    &.active {
      & > span:nth-child(1) {
        transform: translate(10px, 5px) rotate(45deg) scaleX(.5);
      }
      & > span:nth-child(3) {
        transform: translate(10px, -5px) rotate(-45deg) scaleX(.5);
      }
    }
  }
  &__more {
    transition: .25s;
    padding: 12px 20px;
    display: flex;
    position: relative;
    &:hover {
      background-color: $dgray;
      // box-shadow: inset 0 0 0 2px #ffffff;
    }
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      // top: 0;
      background-color: transparent;
      width: 100%;
      height: 2px;
      transition: .25s;
    }
    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
    &:hover::before, &:hover::after {
      background-color: #ffffff;
    }
  }
  &__more-icon {
    width: 20px;
    height: 20px;
    margin-right: 15px;
  }
  &__more-label {
    color: #ffffff;
    font-weight: 300;
    opacity: 0;
    transition: .1s;
  }
  &.shown &__more-label {
    opacity: 1;
    transition: .5s;
  }
  &.hidden {
    transform: translateX(210px);
  }
  &.shown {
    transform: translateX(0);
  }
}

.filter {
  &__item {
    transition: .25s;
    position: relative;
    &:hover {
      background-color: $dgray;
    }
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      background-color: transparent;
      width: 100%;
      height: 2px;
      transition: .25s;
    }
    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
    &:hover::before, &:hover::after {
      background-color: #ffffff;
    }
  }
  &__link {
    padding: 12px 20px;
    display: flex;
  }
  &__icon {
    width: 20px;
    height: 20px;
    margin-right: 15px;
  }
  &__label {
    color: #ffffff;
    font-weight: 300;
    opacity: 0;
    transition: .1s;
  }
  &.full &__label {
    opacity: 1;
    transition: .5s;
  }
}