.header {
  font-weight: 300;
  &__inner {
    display: flex;
    // flex-wrap: wrap;
    // align-items: center;
  }
  &__logo {
    // align-self: stretch;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 250px;
    min-width: 150px;
    width: 100%;
  }
  &__column {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  &__top {
    background-color: #2F2F2F;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
  }
  &__auth {
    color: $llgray;
    transition: .25s;
    &:hover {
      color: #fff;
    }
  }
  &__currency {
    margin-left: 20px;
    color: $llgray;
    & > option {
      background-color: #2F2F2F;
      color: $llgray;
    }
  }
  &__search {
    margin-left: 40px;
  }
  &__shop {
    padding: 20px 10px;
    text-transform: uppercase;
    color: #fff;
    background-color: #000000;
    margin-left: 20px;
    transition: .25s;
    &:hover {
      background-color: #fff;
      color: #000;
    }
  }
  &__bottom {
    background-color: $lgray;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px;
  }
  &__nav {}
  &__other {
    display: flex;
    align-items: center;
    margin-left: 30px;
  }
  &__bottom-dropdown {
    margin-left: 5px;
    & > option {
      background-color: $lgray;
    }

  }
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  &__item {
    color: #fff;
    transition: .25s;
    &:hover {
      opacity: .5;
    }
    & + & {
      margin-left: 40px;
    }
    &--dropdown {
      display: flex;
      align-items: center;
      &::after {
        content: '';
        display: block;
        width: 7px;
        height: 7px;
        border-right: 1px solid #fff;
        border-bottom: 1px solid #fff;
        transform: rotate(45deg);
        position: absolute;
        right: -13px;
        top: 4px;
      }
    }
  }
  &__link {}
}

.logo-text {
  position: absolute;
  left: 33%;
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}